___CSS_LOADER_EXPORT___.push([module.id, `form {
	top: 50%;
	left: 50%;
	display: block;
	width: 100%;
	max-width: 400px;
	margin: 0;
	padding: 10px;
	box-sizing: border-box;
	border-radius: 0.5em;
	font-family: 'Source Sans Pro', sans-serif;
	-webkit-user-select: none;
	   -moz-user-select: none;
	        user-select: none;
}

form .svgContainer {
	position: relative;
	width: 80px;
	height: 80px;
	margin: 0 auto;
	border-radius: 50%;
	background: none;
	border: solid 2.5px #3a5e77;
	overflow: hidden;
	pointer-events: none;
}

form .svgContainer div {
	position: relative;
	width: 100%;
	height: 0;
	overflow: hidden;
	padding-bottom: 100%;
}

form .svgContainer .mySVG {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
}

form .inputGroup {
	width: 80%;
	margin: 5px 5px;
	padding: 0;
	position: relative;
}

form .inputGroup:last-of-type {
	margin-bottom: 0;
}

form label {
	display: block;
	color: #f1ffcf;
	font-weight: 700;
	font-family: inherit;
	margin: 0;
}

form input[type='email'],
form input[type="text"],
form input[type='password'] {
	display: block;
	margin: 0;
	padding: 0 1em 0;
	background-color: #f3fafd;
	border: solid 2px #217093;
	border-radius: 4px;
	-webkit-appearance: none;
	box-sizing: border-box;
	width: 100%;
	height: 30px;
	color: #353538;
	font-weight: 600;
	font-family: inherit;
	transition: box-shadow 0.2s linear, border-color 0.25s ease-out;
}

form input[type='email']:focus,
form input[type="text"]:focus,
form input[type='password']:focus {
	outline: none;
	box-shadow: 0px 2px 10px rgba(0, 0, 0, .1);
	border: solid 2px #4eb8dd;
}

form input[type='email'],
form input[type="text"] {
	letter-spacing: 1px;
	padding: 3px 10px;
}

form button {
	display: block;
	margin: 0;
	/* padding: 0.65em 1em 1em; */
	background-color: #4eb8dd;
	border: none;
	border-radius: 4px;
	box-sizing: border-box;
	box-shadow: none;
	width: 100%;
	height: 30px;
	color: #fff;
	font-weight: 600;
	font-family: inherit;
	transition: background-color 0.2s ease-out;
}

form button:hover,
form button:active {
	background-color: #217093;
	cursor: pointer;
}

form .inputGroup1 .helper {
	position: absolute;
	z-index: 1;
	font-family: inherit;
}

form .inputGroup1 .helper1 {
	top: 0;
	left: 0;
	transform: translate(1.4em, 2.6em) scale(1);
	transform-origin: 0 0;
	color: #217093;
	font-weight: 400;
	opacity: 0.65;
	pointer-events: none;
	transition: transform 0.2s ease-out, opacity 0.2s linear;
}

form .inputGroup1.focusWithText .helper {
	/*input[type='email']:focus + .helper {
	   */
	transform: translate(1.4em, 2em) scale(0.65);
	opacity: 1;
}

.btn-sign {
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	        user-select: none;
}

.btn-sign:hover {
	background-color: rgba(152, 52, 72, 0.697);
	color: red;
}`, "",{"version":3,"sources":["webpack://./src/popup/form.css"],"names":[],"mappings":"AAAA;CACC,QAAQ;CACR,SAAS;CACT,cAAc;CACd,WAAW;CACX,gBAAgB;CAChB,SAAS;CACT,aAAa;CACb,sBAAsB;CACtB,oBAAoB;CACpB,0CAA0C;CAC1C,yBAAiB;IAAjB,sBAAiB;SAAjB,iBAAiB;AAClB;;AAEA;CACC,kBAAkB;CAClB,WAAW;CACX,YAAY;CACZ,cAAc;CACd,kBAAkB;CAClB,gBAAgB;CAChB,2BAA2B;CAC3B,gBAAgB;CAChB,oBAAoB;AACrB;;AAEA;CACC,kBAAkB;CAClB,WAAW;CACX,SAAS;CACT,gBAAgB;CAChB,oBAAoB;AACrB;;AAEA;CACC,kBAAkB;CAClB,OAAO;CACP,MAAM;CACN,WAAW;CACX,YAAY;CACZ,oBAAoB;AACrB;;AAEA;CACC,UAAU;CACV,eAAe;CACf,UAAU;CACV,kBAAkB;AACnB;;AAEA;CACC,gBAAgB;AACjB;;AAEA;CACC,cAAc;CACd,cAAc;CACd,gBAAgB;CAChB,oBAAoB;CACpB,SAAS;AACV;;AAEA;;;CAGC,cAAc;CACd,SAAS;CACT,gBAAgB;CAChB,yBAAyB;CACzB,yBAAyB;CACzB,kBAAkB;CAClB,wBAAwB;CACxB,sBAAsB;CACtB,WAAW;CACX,YAAY;CACZ,cAAc;CACd,gBAAgB;CAChB,oBAAoB;CACpB,+DAA+D;AAChE;;AAEA;;;CAGC,aAAa;CACb,0CAA0C;CAC1C,yBAAyB;AAC1B;;AAEA;;CAEC,mBAAmB;CACnB,iBAAiB;AAClB;;AAEA;CACC,cAAc;CACd,SAAS;CACT,6BAA6B;CAC7B,yBAAyB;CACzB,YAAY;CACZ,kBAAkB;CAClB,sBAAsB;CACtB,gBAAgB;CAChB,WAAW;CACX,YAAY;CACZ,WAAW;CACX,gBAAgB;CAChB,oBAAoB;CACpB,0CAA0C;AAC3C;;AAEA;;CAEC,yBAAyB;CACzB,eAAe;AAChB;;AAEA;CACC,kBAAkB;CAClB,UAAU;CACV,oBAAoB;AACrB;;AAEA;CACC,MAAM;CACN,OAAO;CACP,2CAA2C;CAC3C,qBAAqB;CACrB,cAAc;CACd,gBAAgB;CAChB,aAAa;CACb,oBAAoB;CACpB,wDAAwD;AACzD;;AAEA;CACC;KACI;CACJ,4CAA4C;CAC5C,UAAU;AACX;;AAEA;CACC,eAAe;CACf,yBAAiB;IAAjB,sBAAiB;SAAjB,iBAAiB;AAClB;;AAEA;CACC,0CAA0C;CAC1C,UAAU;AACX","sourcesContent":["form {\r\n\ttop: 50%;\r\n\tleft: 50%;\r\n\tdisplay: block;\r\n\twidth: 100%;\r\n\tmax-width: 400px;\r\n\tmargin: 0;\r\n\tpadding: 10px;\r\n\tbox-sizing: border-box;\r\n\tborder-radius: 0.5em;\r\n\tfont-family: 'Source Sans Pro', sans-serif;\r\n\tuser-select: none;\r\n}\r\n\r\nform .svgContainer {\r\n\tposition: relative;\r\n\twidth: 80px;\r\n\theight: 80px;\r\n\tmargin: 0 auto;\r\n\tborder-radius: 50%;\r\n\tbackground: none;\r\n\tborder: solid 2.5px #3a5e77;\r\n\toverflow: hidden;\r\n\tpointer-events: none;\r\n}\r\n\r\nform .svgContain